:root
{
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App{
    margin-top: 6rem; 
    padding-left: 4rem;
    padding-right: 4rem;
    overflow-x: hidden; 
     
}

@media screen and (max-width: 480px){
  .App{
    margin-top: 4rem; 
    padding-left: .2rem;
    padding-right: .2rem;
    /* overflow-x: hidden;  */
     
} 
}

.button {
  background: linear-gradient(180deg, #fdc50f 26.71%, #fb982f 99.36%);
  /* box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42); */
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 16px;
  padding: 11px 26px 11px 26px;
  margin-right: 10rem;
}

.button:hover {
  background: white;
  cursor: pointer;
  border: 1px solid var(--orange);
  color: var(--orange);
}